import "./Card.css"

const ProductCard = ({ product }) => {
    return(
        <div id="frame">
            <h1 id="product_name">{product.Name}</h1>
            <img id="product_img" src={product.Image} alt={product.Name}/>
            <a id="product_link" target="_blank" href={product.Link} rel="noreferrer noopener">Buy on Amazon</a>
        </div>
    )
}

export default ProductCard;