import './Home.css'
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link
} from "react-router-dom";
import InstagramIcon from '@mui/icons-material/Instagram';
import YoutubeIcon from '@mui/icons-material/YouTube';
import Outfit from './Outfit'
import OutfitList from './OutfitList';
import Main from "./ProductList";
import logoTFF4A from "./logo_tff4a.png"


const TikTokIcon = () => {
    return (
        <svg
            fill='rgb(255, 245, 242)'
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 50 50"
            width="100%"
            height="100%"
        >
            <path d="M41,4H9C6.243,4,4,6.243,4,9v32c0,2.757,2.243,5,5,5h32c2.757,0,5-2.243,5-5V9C46,6.243,43.757,4,41,4z M37.006,22.323 c-0.227,0.021-0.457,0.035-0.69,0.035c-2.623,0-4.928-1.349-6.269-3.388c0,5.349,0,11.435,0,11.537c0,4.709-3.818,8.527-8.527,8.527 s-8.527-3.818-8.527-8.527s3.818-8.527,8.527-8.527c0.178,0,0.352,0.016,0.527,0.027v4.202c-0.175-0.021-0.347-0.053-0.527-0.053 c-2.404,0-4.352,1.948-4.352,4.352s1.948,4.352,4.352,4.352s4.527-1.894,4.527-4.298c0-0.095,0.042-19.594,0.042-19.594h4.016 c0.378,3.591,3.277,6.425,6.901,6.685V22.323z" />
        </svg>
    );
};

function MainMenu() {
    return (
        <div id="menu_wrapper">
            <Link id="menu_choose" to="/outfits"><h1>Discover outfits &#8594;</h1></Link>
            <Link id="menu_choose" to="/products"><h1>Discover products &#8594;</h1></Link>
        </div>
    )
}


export default function Home() {
    return (
        <Router>
            <div id="layout">
                <div id="navbar">
                    <Link id="home_btn" to="/"><img id="logo_image" src={logoTFF4A} /></Link>
                    <div id="menus">
                        <div id="section">
                            <Link to="/outfits">OUTFITS</Link>
                        </div>
                        <div id="section">
                            <Link to="/products">PRODUCTS</Link>
                        </div>
                    </div>
                </div>
                <div id="content">
                    <Routes>
                        <Route path="/" element={<MainMenu />} />
                        <Route path="/outfit/:id/:OutfitProducts" element={<Outfit />} />
                        <Route path="/outfits" element={<OutfitList />} />
                        <Route path="/products" element={<Main />} />
                    </Routes>
                </div>
                <div id="footer">
                    <div id="container">
                        <a id="icons" href=""><InstagramIcon fontSize="inherit" /></a>
                        <a id="icons" href=""><YoutubeIcon fontSize='inherit' /></a>
                        <a id="icons" href=""><TikTokIcon /></a>
                    </div>
                    <code>&copy; All rights reserved. 2022</code>
                </div>
            </div>
        </Router>
    )
}