import "./Card.css"
import { Link } from "react-router-dom";

const OutfitCard = ({ outfit }) => {

    return(
        <div id="frame">
            <h1 id="product_name">{`Outfit ${outfit.id}`}</h1>
            <img id="product_img" src={outfit.Image} alt={`Outfit ${outfit.id}`}/>
            <Link id="product_link" to={`/outfit/${outfit.id}/${outfit.products}`}>Discover</Link>
        </div>
    )
}

export default OutfitCard;